/**
 * axios 二次封装
 * 请求拦截、响应拦截、错误统一处理
 */
import axios from 'axios';
import httpConfig from '@/config/httpConfig'; // 导入自定义 axios 请求配置项
import router from '@/router';
import store from '@/store';
import { LOGIN_OUT, UPDATE_NETWORK } from '@/store/mutation-types';

/**
 * 判断是否为管理端
 */
const isAdmin = () => {
  return (
    router.currentRoute.path.match(/^\/admin$/) ||
    router.currentRoute.path.match(/^\/admin\//)
  );
};

/**
 * 跳转登录页 （需要区分客户端/管理端）
 * 使用查询参数携带当前页面路由，以期在登录页面完成登录后返回当前页面
 */
const toLogin = () => {
  router.replace({
    path: isAdmin() ? '/admin/login' : '/login',
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
};

/**
 * 开始 loading...
 */
const startLoading = () => {
  // 打开操作
  // store.state.overlay = true;
  store.state.loading = true;
  // store.state.skeletonLoader = true;
};

/**
 * 结束 loading...
 */
const endLoading = () => {
  // 关闭操作
  // store.state.overlay = false;
  store.state.loading = false;
  // store.state.skeletonLoader = false;
};

// showFullScreenLoading() tryHideFullScreenLoading() 要干的事儿就是将同一时刻的请求合并。
// 声明一个变量 needLoadingRequestCount，每次调用showFullScreenLoading方法 needLoadingRequestCount + 1。
// 调用tryHideFullScreenLoading()方法，needLoadingRequestCount - 1。
// needLoadingRequestCount为 0 时，结束 loading。

// 需要加载请求数
let needLoadingRequestCount = 0;

/**
 * 显示全屏加载
 */
const showFullScreenLoading = () => {
  needLoadingRequestCount === 0 && startLoading();
  needLoadingRequestCount++;
};

/**
 * 尝试隐藏全屏加载
 */
const tryHideFullScreenLoading = () => {
  if (needLoadingRequestCount <= 0) return;
  needLoadingRequestCount--; // 必须先减一操作后才进行下一步的判断
  needLoadingRequestCount === 0 && endLoading();
};

/**
 * 请求失败的消息提示
 */
const tip = (message: string) => {
  // 非生产环境下打印错误消息
  // process.env.NODE_ENV !== 'production' && console.log(message);
  return message;
  // 消息提示相关操作
  // ...
};

/**
 * 请求失败统一处理函数
 * @param {number} status - 请求失败状态码
 */
const errorHandler = (status: number, message: string) => {
  switch (status) {
    case 400:
      // 错误请求（请求无效）
      tip(`${status}：错误请求`);
      break;

    case 401:
      // 未授权（未登录），请重新登录
      tip(`${status}：未授权，请重新登录`);
      // 跳转到登录页
      toLogin();
      break;

    case 403:
      // 拒绝访问（禁止访问）
      // token过期
      // a. 对用户进行登录过期提示
      tip(`${status}：拒绝访问`);
      // b. 清除本地token和清空vuex中token对象 （需要区分客户端/管理端）
      store.commit({
        type: LOGIN_OUT,
        isAdmin: isAdmin() ? true : false
      });
      // c. 跳转登录页面
      setTimeout(() => {
        toLogin();
      }, 1000);
      break;

    case 404:
      // 请求错误，未找到该资源（请求不存在）
      tip(`${status}：请求错误，未找到该资源`);
      break;

    case 405:
      // 请求方法未允许
      tip(`${status}：请求方法未允许`);
      break;

    case 408:
      // 请求超时
      tip(`${status}：请求超时`);
      break;

    case 500:
      // 服务器出错
      tip(`${status}：服务器出错`);
      break;

    case 501:
      // 网络未实现
      tip(`${status}：网络未实现`);
      break;

    case 502:
      // 网络错误
      tip(`${status}：网络错误`);
      break;

    case 503:
      // 服务不可用
      tip(`${status}：服务不可用`);
      break;

    case 504:
      // 网络超时
      tip(`${status}：网络超时`);
      break;

    case 505:
      // HTTP版本不支持该请求
      tip(`${status}：HTTP版本不支持该请求`);
      break;

    default:
      // 其他错误,直接抛出错误提示
      tip(`${status}：${message}`);
  }
};

/**
 * 创建 axios 实例
 */
const instance = axios.create(httpConfig);
// 设置 post 请求头默认值
instance.defaults.headers.post['Content-Type'] =
  'application/x-www-form-urlencoded';

/**
 * 请求拦截器
 * 0. 开启 loading...（只在无错误发生时才开启）
 * 1. token 校验 （需要区分客户端/管理端）
 */
instance.interceptors.request.use(
  config => {
    // 在发送请求之前做点什么

    // 0-1. 请求 loading... 开始加载
    showFullScreenLoading();

    // 1. token 校验：（需要区分客户端/管理端）
    // 登录流程控制中，根据本地是否存在token判断用户的登录情况
    // （一般是在登录完成之后，将用户的token通过 localStorage / sessionStorage 或者 cookie 存在本地，然后设置vuex中的token状态）
    // 然后用户每次在进入（刷新）页面的时候（即在全局前置导航守卫中），会首先从vuex中读取token，如果token存在说明用户已经登录过则更新vuex中的token状态;
    // a. 每次发送请求之前判断vuex中是否存在token
    // b. 如果存在，则统一在请求头对象上都加上token，这样后台根据token判断你的登录情况（已登录 / 未登录 / token过期）
    // c. 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行一些统一的操作
    const token = isAdmin()
      ? store.state.Auth.adminToken
      : store.state.Auth.userToken;
    token && (config.headers.Authorization = token);

    // 最后必须返回 config 对象
    return config;
  },
  error => {
    // 处理请求错误
    return Promise.reject(error);
  }
);

/**
 * 响应拦截器
 * 0. 关闭 loading...（无论是成功还是失败都得关闭）
 */
instance.interceptors.response.use(
  response => {
    // 2xx范围内的任何状态代码都会触发此函数处理响应数据
    // 0-2. 请求 loading... 加载完成（关闭）
    tryHideFullScreenLoading();

    // 请求成功
    // （如果返回的状态码为200，说明接口请求成功，可以正常拿到数据）
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    // 任何超出2xx范围的状态代码都会触发此功能处理响应错误
    // 0-3. 请求 loading... 加载完成（关闭）
    tryHideFullScreenLoading();

    // 请求失败
    if (error.response) {
      // 处理响应错误
      errorHandler(error.response.status, error.response.statusText);
      return Promise.reject(error);
    } else if (!navigator.onLine) {
      // 处理断网的情况
      // 列如：请求超时或断网时，更新state的 network 状态为false
      // network 状态在app.vue中控制着一个全局的断网提示组件的显示隐藏
      store.commit(UPDATE_NETWORK, false);
    } else {
      return Promise.reject(error);
    }
  }
);

export default instance;
