import Vue from 'vue';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';
import vuetify from './plugins/vuetify';
import '@/assets/styles/common.scss'; // 导入全局样式
import api from '@/api'; // 导入api接口

// 图标字体 font-class 方式引用
import '@/assets/iconfonts/D3III16glBasic/iconfont.css';
import '@/assets/iconfonts/D3III16gfBasic/iconfont.css';
import '@/assets/iconfonts/D3III20glBasic/iconfont.css';
import '@/assets/iconfonts/D3III20gfBasic/iconfont.css';
import '@/assets/iconfonts/D3III24glBasic/iconfont.css';
import '@/assets/iconfonts/D3III24gfBasic/iconfont.css';
import '@/assets/iconfonts/D3III16glMobius/iconfont.css';

// // 图标字体 Symbol 方式引用（ts中导入不支持）
// import '@/assets/iconfonts/D3III16glBasic/iconfont.js';
// import '@/assets/iconfonts/D3III16gfBasic/iconfont.js';
// import '@/assets/iconfonts/D3III20glBasic/iconfont.js';
// import '@/assets/iconfonts/D3III20gfBasic/iconfont.js';
// import '@/assets/iconfonts/D3III24glBasic/iconfont.js';
// import '@/assets/iconfonts/D3III24gfBasic/iconfont.js';
// import '@/assets/iconfonts/D3III16glMobius/iconfont.jS';

/**
 * 增强 Vue 类型 （https://cn.vuejs.org/v2/guide/typescript.html#增强类型以配合插件使用）
 */
// 1. 确保在声明补充的类型之前导入 'vue'
// import Vue from 'vue';
// 2. 定制一个文件，设置你想要补充的类型
// 在 types/vue.d.ts 里 Vue 有构造函数类型
declare module 'vue/types/vue' {
  // 3. 声明为 Vue 补充的东西
  interface Vue {
    $api: any;
  }
}

Vue.prototype.$api = api; // 将api挂载到vue的原型上

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app');
