/**
 * 根级别（全局）的 state
 */
import RootState from '@/types/store/rootState.interface'; // 导入根级别的 state 参数类型

const state: RootState = {
  title: '', // 当前页面标题
  breadcrumbs: [], // 当前页面面包屑导航
  network: true, // 网络状态（请求超时或断网）
  overlay: false, // 页面遮罩层状态
  loading: false, // 页面加载状态 / HTTP请求加载状态
  skeletonLoader: false // 页面骨架加载器
};

export default state;
