/**
 * 根级别（全局）的 mutations
 */
import {
  UPDATE_TITLE,
  UPDATE_BREADCRUMBS,
  UPDATE_NETWORK
} from './mutation-types';
import RootState from '@/types/store/rootState.interface'; // 导入根级别的 state 参数类型

export default {
  // 更新当前页面标题
  [UPDATE_TITLE](state: RootState, title: string) {
    state.title = title;
    document.title = `${title} - UXDCode`; // 设置页面 document 的 title（Vuex动态标题）
  },

  // 更新当前页面面包屑导航
  [UPDATE_BREADCRUMBS](state: RootState, breadcrumbs: object[]) {
    state.breadcrumbs = breadcrumbs;
  },

  // 更新当前网络状态
  [UPDATE_NETWORK](state: RootState, network: boolean) {
    state.network = network;
  }
};
