/**
 * Vue Router 路由出口文件
 */
import Vue from 'vue';
import VueRouter from 'vue-router';
import routes from './routes';
import store from '@/store';
import { LOGIN_IN, UPDATE_NETWORK } from '@/store/mutation-types';
import { getToken } from '@/utils/common';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  // 路由的滚动行为
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      // 在通过按下浏览器的 后退/前进 按钮时，就会像浏览器的原生表现那样
      return savedPosition;
    } else {
      // 如果将要离开的页面是需要缓存的页面，则保存原来的垂直滚动位置到路由元信息，以便在页面后退时可以滚动到离开时原来的位置
      if (from.meta.keepAlive) {
        from.meta.savedPosition =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
      }
      // 回到页面原来位置 / 页面顶部
      return { x: 0, y: to.meta.savedPosition || 0 };
    }
  }
});

/**
 * 全局前置导航守卫
 * 0. 开启 loading...
 * 1. 页面刷新， 重新赋值 vuex 上储存的 token / network / 所有state数据
 *   （必须，因为 vuex 上储存的数据在刷新页面时都会归零回到初始状态）
 *   （需要区分客户端/管理端 和 localStorage / sessionStorage 中不存在token 的情况）
 * 2. 设置页面 document 的 title（路由静态标题 / Vuex动态标题）
 * 3. 验证将要访问的页面是否需要登录权限
 */
router.beforeEach((to, from, next) => {
  // 0-1. 路由 loading... 开始加载
  store.state.overlay = true;
  // store.state.loading = true;
  store.state.skeletonLoader = true;

  // 判断是否为管理端
  const isAdmin = to.path.match(/^\/admin$/) || to.path.match(/^\/admin\//);

  // 1-1. 页面刷新时，重新赋值 token （需要区分客户端/管理端 和 localStorage / sessionStorage 中不存在token 的情况）
  // a. 初次未登录的情况下刷新/访问需要/不需要授权路由：不重新赋值token （sessionStorage 中不存在 token）
  // b. 已登录了刷新/访问其他路由：重新赋值token （sessionStorage 已储存 token）

  /* store.commit({
    type: LOGIN_IN,
    isAdmin: isAdmin ? true : false,
    token: isAdmin ? getToken('adminToken') : getToken() // 存在赋值 字符串'null' 的边界问题
  }); */
  // 上面的写法会引发一个问题：（边界问题）（注意：localStorage / sessionStorage 存储数据的格式都是以 string 字符串的形式来存储的）
  // 在初次未登录的情况下去刷新/访问需要/不需要授权的路由时，
  // 由于 sessionStorage 中还没有储存到 token 值，
  // 那么通过 getToken('XXX') 取出来值为 null（object 类型），
  // 然后在触发 vuex 上的 LOGIN_IN 时，执行了 setToken('XXX', null) 对 sessionStorage 进行赋值，由于 sessionStorage 只能存储 string 类型数据，那么 null（object 类型）会被隐式自动转成了 'null'（string类型），
  // 这时 sessionStorage 中的 token 值为 'null' 而不是 null，在下面的验证授权逻辑中 if (!token)  ===>  if (!'null')  ===>  false，所以与我们期望的 if (!null)  ===>  true 结果相反了。

  // 下面为改良方法：
  // 如果 getToken('XXX') 结果为null，那么肯定是初次未登录的情况，
  // 如果是登录了再刷新/访问其他路由的，那么肯定在登录是触发 vuex 上的 LOGIN_IN，sessionStorage 中的 token 值肯定为非空，
  // 这样可以过滤掉：“在初次未登录的情况下去访问需要授权的路由时” 的边界问题
  if (isAdmin && getToken('adminToken')) {
    // 管理端 && getToken('adminToken') !== null
    store.commit({
      type: LOGIN_IN,
      isAdmin: true,
      token: getToken('adminToken')
    });
  } else if (!isAdmin && getToken()) {
    // 客户端 && getToken() !== null
    store.commit({
      type: LOGIN_IN,
      isAdmin: false,
      token: getToken()
    });
  }

  // 1-2. 页面刷新时，重新更新全局state的 network 状态为true
  store.commit(UPDATE_NETWORK, true);

  // 2-1. 设置页面 document 的 title（路由元信息的静态标题）
  !to.meta.isDynamicTitle &&
    to.meta.title &&
    (document.title = `${to.meta.title} - UXDCode`);
  // 2-2. 设置页面 document 的 title （vuex 上储存的动态标题（例如文章详情页动态路由的标题））
  // （需在组件的 beforeRouteEnter 钩子里触发根级别的 UPDATE_TITLE，并且在根级别的 UPDATE_TITLE 里面设置页面 document 的 title）

  // 3. 验证将要访问的页面是否需要登录权限（需要区分客户端/管理端）
  const loginPath = isAdmin ? '/admin/login' : '/login';
  // 获取 vuex 上的 token
  const token = isAdmin
    ? store.state.Auth.adminToken
    : store.state.Auth.userToken;
  // 获取浏览器 localStorage / sessionStorage 上的 token
  const token2 = isAdmin ? getToken('adminToken') : getToken();
  // console.log(loginPath, token);
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 此路由需要验证，请检查是否已登录
    // 如果没有，请重定向到登录页。
    if (!token || !token2) {
      // vuex 或 localStorage / sessionStorage 上不存在 token，未登录，强制跳转到登录页
      // （如果只判断 vuex 上的 token，在登录后进入首页后，使用浏览器前进按钮进入登录页手动清除浏览器 localStorage / sessionStorage 上 token，
      // 然后使用浏览器后退按钮进入首页则不会自动跳转到登录页了，只有再次刷新首页才会自动跳转到登录页）

      // console.log(getToken('adminToken'), typeof getToken('adminToken'));  // 预期：null object
      // console.log(getToken(), typeof getToken());  // 预期：null object
      // console.log(token, typeof token);  // 预期：'' string
      next({
        path: loginPath,
        query: { redirect: to.fullPath } // 将跳转的路由 fullPath 作为查询参数，以便在登录成功后再跳转回到该路由 （登录后返回当前页面）
        // 登录后需要根据以下情况再次跳转路由：（需在登录页面模块中处理）
        // 1. 查询参数为空时，跳转到首页
        // 2. 查询参数不为空时，跳转到查询参数上携带的路由 fullPath （即返回在登录时的上一个访问的页面）
      });
    } else {
      // vuex 上存在 token，已登录，直接放行
      next();
    }
  } else {
    next(); // 确保一定要调用 next()
  }
});

/**
 * 全局后置导航守卫
 * 0. 关闭 loading...
 */
router.afterEach((to, from) => {
  // 0-2. 路由 loading... 加载完成（关闭）
  setTimeout(() => {
    store.state.overlay = false;
    // store.state.loading = false;
    store.state.skeletonLoader = false;
  }, 200);
});

export default router;
