/**
 * 公共函数定义处
 */

/**
 * 取存删 token
 * 注意：localStorage / sessionStorage 存储数据的格式都是以 string 字符串的形式来存储的
 * @param {string} name - token 名称
 * @param {string} token - token 值
 */
export const getToken = (name = 'userToken') => {
  return localStorage.getItem(name);
  // return sessionStorage.getItem(name);
};

export const setToken = (name: string, token: string) => {
  localStorage.setItem(name, token);
  // sessionStorage.setItem(name, token);
};

export const delToken = (name = 'userToken') => {
  localStorage.removeItem(name);
  // sessionStorage.removeItem(name);
};
