/**
 * HomeAdmin 模块接口列表（举例）
 */
import base from '@/api/base'; // 导入接口域名版本列表
import http from '@/utils/http'; // 导入http中封装的axios实例
import { GetNavMenuItemsParams } from '@/types/layouts/baseLayoutAdmin.interface';
// import Qs from 'qs'; // 根据需求是否导入qs模块（用来序列化post类型的数据）

const homeAdmin = {
  // 获取管理员的左侧抽屉菜单列表项
  getNavMenuItems(params: GetNavMenuItemsParams) {
    return http.get(`${base.v1}/admin/menu`, {
      params: params
    });
  }
};

export default homeAdmin;
