/**
 * 客户端登录认证
 */
import { LOGIN_IN, LOGIN_OUT } from '@/store/mutation-types';
import { setToken, delToken } from '@/utils/common';
import { AuthState, AuthMutationPayload } from '@/types/views/auth.interface';

export default {
  state: {
    adminToken: '', // 管理端 token
    userToken: '' // 客户端 token
  },
  getters: {},
  mutations: {
    [LOGIN_IN](state: AuthState, payload: AuthMutationPayload) {
      if (payload.isAdmin) {
        // 管理端
        state.adminToken = payload.token;
        setToken('adminToken', payload.token);
      } else {
        // 客户端
        state.userToken = payload.token;
        setToken('userToken', payload.token);
      }
    },
    [LOGIN_OUT](state: AuthState, payload: AuthMutationPayload) {
      if (payload.isAdmin) {
        // 管理端
        state.adminToken = '';
        delToken('adminToken');
      } else {
        // 客户端
        state.userToken = '';
        delToken();
      }
    }
  },
  actions: {}
};
