/**
 * 路由配置选项
 * meta? - 可选，路由元信息，包含以下参数：
 * @param {boolean} requiresAuth - 页面访问是否需要登录权限 { true: 需要 | false: 不需要 } （false时可以直接不加该字段）
 * @param {boolean} isDynamicTitle - 是否为动态页面标题 / 面包屑导航 { true: 是 | false: 不是 } （false时可以直接不加该字段）
 * @param {string} keepAlive - 是否缓存页面 { true: 缓存 | false: 不缓存 }
 * @param {string} isUseCache - 是否使用缓存数据 { true: 使用 | false: 不使用 }
 * @param {string} savedPosition - 保存页面滚动的位置（window.pageXOffset / pageYOffset || document.documentElement.scrollLeft / scrollTop || document.body.scrollLeft / scrollTop）
 * @param {string} title - 页面标题
 * @param {string} breadcrumbs - 页面面包屑导航
 * @param {string} skeletonLoaderType - 页面骨架加载器类型
 */

