/**
 * 接口域名版本的管理
 */
import { DEV_BASE_API, PROD_BASE_API, TEST_BASE_API } from '@/config'; // 导入API域名配置

// // 环境的自动切换（.env.[mode] 文件）
// const _baseAPI = process.env.VUE_APP_BASE_API;

// 环境切换
let _baseAPI = '';
if (process.env.NODE_ENV === 'development') {
  // 开发环境
  _baseAPI = DEV_BASE_API;
} else if (process.env.NODE_ENV === 'production') {
  // 生产环境（线上环境）
  _baseAPI = PROD_BASE_API;
} else if (process.env.NODE_ENV === 'test') {
  // 测试环境
  _baseAPI = TEST_BASE_API;
}

// 定义请求基准路径（版本管理）
const base = {
  v1: `${_baseAPI}/v1.0`,
  v2: `${_baseAPI}/v2.0`,
  'v2-1': `http://localhost:5000/api/v2.1`,
  v3: `${_baseAPI}/v3.0`
};

export default base;
